<template>
    <div class="col-lg-6  col-xxl-4 mt-4">

        <div class="project-card">

            <div class="project-title">
                
                <h6 class="m-0  pt-1">{{project.slogan.toUpperCase()}} <i v-if="project.link"   class="btn-heart fa fa-external-link-alt fa-heart"></i></h6>

            </div>



            <div class="project-images">
                <div id="" class="carousel slide" data-bs-ride="carousel">
                    <!-- The slideshow/carousel -->
                    <div class="carousel-inner">
                        <div class="carousel-item active">
                            <img src="https://www.dropbox.com/s/jvkx7uft87fkbdh/1660300464.jpg?dl=1"
                                class="d-block w-100" alt="...">
                        </div>
                    </div>
                </div>
            </div>

            <div class="project-infromation p-3">
                <small class="type ">{{ project.year }}</small>
                <h3 class="m-0  pt-1">{{ project.title.toUpperCase() }}</h3>
                <small class=" me-2 text-primary " v-for="(skill, index) in project.skills" :key="index">
                    #{{ skill }}
                </small>
                <br><br>
                <small v-for="(technology, index) in project.technologies" :key="index"
                    :class="'badge me-1 ' + setTagColor(technology)">{{
                            technology
                    }}</small>
            </div>
            <div class="more-information p-3">
                <small>Description</small>
                <p class="description">
                    <small>
                        {{ project.description }}
                    </small>
                </p>
                <!-- <a href="" class="btn-more text-end"><i class="fa-solid fa-arrow-up-right-from-square"></i> More </a> -->
            </div>
            <div v-if="project.link" class="view-details text-center">
                <a  href="{{project.link}}" data-project="e2674709fb7455ff2db04dec93b7ac1a" data-bs-toggle="modal"
                    data-bs-target="#details_modal" class="btn-view-detail">
                    Visit <i class="fa fa-external-link-alt"></i>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props: ['project'],
    methods: {
        setTagColor(tag) {
            let className = "bg-primary"
            switch (tag) {
                case 'PHP':
                    className = "bg-info";
                    break;
                case 'LARAVEL':
                    className = "bg-danger";
                    break;
                case 'VUE':
                    className = "bg-success";
                    break;


            }
            return className;
        }
    }
}
</script>